import * as React from 'react';
import axios from 'axios';
import './Login.css';

import { Link } from "react-router-dom";

import greencube from '../../../media/img/greencube-logo.svg'

import { SimpleButton } from '../../shared/buttons/SimpleButton/SimpleButton';


interface ILoginProps {
    history: any;
    setLoaginState(loggedIn: boolean): void;
}

interface ILoginState {
    user: string;
    password: string
}

export class Login extends React.Component<ILoginProps, ILoginState> {

    public state = {
        user: '',
        password: ''
    };


    public handleKeyPress = (e: any) => {
        if (e.key === 'Enter') {
            this.dataChecker();
        }
    };

    public handleLogin = () => {
        console.log(`Logging in with username:${this.state.user} and password:${this.state.password}`);
        this.handleSubmit();
    };

    public handleInputChange = (event: any) => {
        const inputId: string = event.currentTarget.id;
        this.state[inputId] = event.currentTarget.value;
        this.setState(this.state);
    };

    public dataChecker = () => {
        if (this.state.user !== '' && this.state.password !== '') {
            this.handleLogin();
        } else {
            console.log('Fields are empty!')
        }
    };

    public switchToRegister = () => {
        console.log('register');
    }

    public handleSubmit = () => {
        const payload = {
            "username": this.state.user,
            "password": this.state.password
        }

        axios.post('https://greencube-api.herokuapp.com/user-ms/authenticate-user', JSON.stringify(payload),
            {
                headers: {
                    'Content-Type': 'application/json'
                }
            })
            .then(res => {
                console.log(res);
                if (res.statusText === 'OK') {
                    localStorage.setItem('username', payload.username);
                    localStorage.setItem('userToken', res.data);
                    // this.props.history.push('/mainpage/live');
                    window.location.href = '/mainpage';
                    localStorage.setItem('plant_id', '0');
                }
            })
    }

    public render() {
        return (
            <div className="login">

                <div className="login-body">
                    <div className="login-header">
                        <div className="logo">
                            <img src={greencube} />
                        </div>
                        <div className="login-title">
                            <p>CubeGreen</p>
                        </div>
                    </div>
                    <div className="login-box">
                        <div className='login-field'>
                            <input id='user'
                                value={this.state.user}
                                onChange={this.handleInputChange}
                                onKeyPress={this.handleKeyPress}
                                placeholder="Username"
                                autoComplete="off"
                            />
                        </div>
                        <div className='login-field'>
                            <input id='password'
                                type="password"
                                value={this.state.password}
                                onChange={this.handleInputChange}
                                onKeyPress={this.handleKeyPress}
                                placeholder="Password"
                            />
                        </div>
                    </div>
                    <div className="buttons-area">
                        <div className="login-button">
                            <SimpleButton title="Login" onClick={this.dataChecker} />
                        </div>
                        <div className="register-button">
                            <Link to="/register"><SimpleButton title="Register" onClick={this.switchToRegister} /></Link>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}


