import * as React from 'react';

import "./LiveImage.css";

interface ILiveImageProps {
    url: string;
}

export class LiveImage extends React.Component<ILiveImageProps> {

    public render() {
        return (
            <div className="live-image-container">
                <img src={this.props.url} />
            </div>
        )
    }
}


