import * as React from 'react';
import { SimpleButton } from '../../shared/buttons/SimpleButton/SimpleButton';
import axios from 'axios';
import '../Register/Register.css';
import greencube from '../../../media/img/greencube-logo.svg'
import { Link } from "react-router-dom";


interface IRegisterProps {
    history: any;
}

interface IRegisterState {
    user: string;
    password: string;
    repassword: string;
}

export class Register extends React.Component<IRegisterProps, IRegisterState> {

    public state = {
        user: '',
        password: '',
        repassword: '',
    };

    public handleKeyPress = (e: any) => {
        if (e.key === 'Enter') {
            this.dataChecker();
        }
    }

    public handleRegister = () => {
        console.log(`Registered with username: ${this.state.user} and password:${this.state.password}`);
        this.handleSubmit();
    };

    public handleInputChange = (event: any) => {
        const inputId: string = event.currentTarget.id;
        this.state[inputId] = event.currentTarget.value;
        this.setState(this.state);
    };

    public dataChecker = () => {
        if (this.state.user === '' || this.state.password === '' || this.state.repassword === '') {
            console.log('Fields are empty!');
        } else {
            this.handleRegister();
        }
    };

    private switchToLogin = () => {
        console.log("Send to login");
    }

    public handleSubmit = () => {
        const payload = {
            "username": this.state.user,
            "password": this.state.password
        }

        console.log(payload);

        axios.post('https://greencube-api.herokuapp.com/user-ms/new-user', JSON.stringify(payload) ,
            {
                headers: {
                    'Content-Type': 'application/json'
                }
            })
            .then(res => {
                console.log(res);
                if(res.statusText === 'OK'){
                    this.props.history.push('/login');
                }
            })
    }
    

    public render() {
        return (
            <div className="register">
                <div className="register-body">
                    <div className="register-header">
                        <div className="logo">
                            <img src={greencube} />
                        </div>
                        <div className="register-title">
                            <p>CubeGreen</p>
                        </div>
                    </div>
                    <div className="register-box">
                        <div className='register-field'>
                            <input
                                id='user'
                                value={this.state.user}
                                onChange={this.handleInputChange}
                                onKeyPress={this.handleKeyPress}
                                placeholder="Username"
                                autoComplete="off"
                            />
                        </div>
                        <div className='register-field'>
                            <input
                                id='password'
                                type="password"
                                value={this.state.password}
                                onChange={this.handleInputChange}
                                onKeyPress={this.handleKeyPress}
                                placeholder="Password"
                            />
                        </div>
                        <div className='register-field'>
                            <input
                                id='repassword'
                                type="password"
                                value={this.state.repassword}
                                onChange={this.handleInputChange}
                                onKeyPress={this.handleKeyPress}
                                placeholder="Confirm Password"
                            />
                        </div>
                    </div>
                    <div className="buttons-area">
                        <div className="register-button">
                            <SimpleButton title="Register" onClick={this.dataChecker} />
                        </div>
                        <div className="back-to-login-button">
                            <Link to="/login"><SimpleButton title="Back to login" onClick={this.switchToLogin} /></Link>
                        </div>
                    </div>
                </div>

            </div>
        )
    }
}


