import * as React from "react";
import axios from 'axios';
import './Store.css';
import { SimpleButton } from "../../shared/buttons/SimpleButton/SimpleButton";
import { ScrollableMenu } from "../../shared/ScrollableMenu/ScrollableMenu";
import { Popup } from "./Popup";
import { IPlant } from "../LiveView/LiveView";
import { Loading } from "../../shared/Loading/Loading";
// interface HomeProps {

// }

interface IStoreState {
    coins: number;
    showPopup: boolean;
    plantList: IPlant[];
    isLoaded: boolean;
}

export class Store extends React.Component<any, IStoreState> {


    public state = {
        coins: 0,
        showPopup: false,
        plantList: [],
        isLoaded: false,
    };

    public componentWillMount = () => {
        if (!localStorage.getItem('userToken')) {
            this.props.history.push('/login');
        }

        const payload = {
            "username": localStorage.getItem('username'),
        }

        axios.post('https://greencube-api.herokuapp.com/user-ms/user-data', JSON.stringify(payload),
            {
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': localStorage.getItem('userToken'),
                }
            })
            .then(res => {
                console.log(res);
                if (res.statusText === 'OK') {
                    this.setState({
                        coins: res.data.credits,
                        isLoaded: true,
                    })
                }
            })
        axios.get('https://greencube-api.herokuapp.com/plant-ms/available')
            .then(res => {
                console.log(res);
                this.setState({
                    plantList: res.data
                })
            })

    }

    public togglePopup = () => {
        this.setState({
            showPopup: !this.state.showPopup,
        });
    }

    public render() {
        return (
            <div className="store">
                <div className="first-container">
                    <div className="plants-to-buy-title">
                        <p>Plants Store</p>
                    </div>
                    <div className="plants-to-buy">
                        <div className="plants-to-buy-content">
                            {
                                this.state.isLoaded ?
                                    <ScrollableMenu plantList={this.state.plantList} hasPrice={true} balance={this.state.coins} />
                                    :
                                    <Loading />
                            }

                        </div>
                    </div>
                </div>

                <div className="add-funds">
                    <SimpleButton title="Add Funds" onClick={this.togglePopup} />
                    <div className="coinsBalance">
                        {
                            this.state.isLoaded ?
                                <p>{this.state.coins} CB</p>
                                :
                                <p>...</p>
                        }

                    </div>
                </div>

                {
                    this.state.showPopup &&
                    <Popup
                        closePopup={this.togglePopup}
                    />
                }
            </div>
        )
    }
}



