import * as React from 'react';
import axios from 'axios';
import { PlantItem } from '../PlantItem/PlantItem';
import './ScrollableMenu.css';
import { IPlant } from '../../pages/LiveView/LiveView';
import * as PropTypes from 'prop-types';

interface IScrollableMenuProps {
    hasPrice: boolean,
    balance?: number,
    plantList: IPlant[],
}

export class ScrollableMenu extends React.Component<IScrollableMenuProps, {}>{

    public static contextTypes = { router: PropTypes.object };

    public handleLiveRedirect = (plantId: number) => {
        if (!this.props.hasPrice) {
            console.log("Clicked on plant " + plantId);
            localStorage.setItem("plant_id", plantId.toString());
            this.context.router.history.push('./live');
        } else {

            const payload = {
                "username": localStorage.getItem('username'),
                "plant_id": plantId.toString(),
            }

            axios.post('https://greencube-api.herokuapp.com/plant-ms/transfer', JSON.stringify(payload),
                {
                    headers: {
                        'Content-Type': 'application/json'
                    }
                })
                .then(res => {
                    console.log(res);
                    if (res.statusText === 'OK') {
                        console.log(res);
                        window.location.reload();
                    }
                })
        }

    }

    public render() {
        return (
            <div className="scrollable-menu">
                {
                    this.props.plantList.map((plant, index) => {
                        return (
                            <PlantItem
                                key={index}
                                hasPrice={this.props.hasPrice}
                                price={plant.price}
                                balance={this.props.balance}
                                name={plant.type}
                                onClick={this.handleLiveRedirect}
                                id={plant.plant_id}
                            />
                        )
                    })
                }
            </div>
        )
    }
}


