import * as React from "react";
import axios from 'axios';
import './Home.css';
import { ScrollableMenu } from "../../shared/ScrollableMenu/ScrollableMenu";
import { IPlant } from "../LiveView/LiveView";
import { Loading } from "../../shared/Loading/Loading";

// interface HomeProps {

// }


interface IHomeState {
    plantList: IPlant[],
    isLoaded: boolean,
}

export class Home extends React.Component<any, IHomeState> {

    public state = {
        plantList: [],
        isLoaded: false,
    }

    public componentWillMount = () => {
        if (!localStorage.getItem('userToken')) {
            this.props.history.push('/login');
        }
    }

    public componentDidMount = () => {
        const payload = {
            "username": localStorage.getItem('username'),
        }

        axios.post('https://greencube-api.herokuapp.com/plant-ms/owned', JSON.stringify(payload),
            {
                headers: {
                    'Content-Type': 'application/json'
                }
            })
            .then(res => {
                console.log(res);
                if(res.statusText === 'OK'){
                    this.setState({
                        plantList: res.data,
                        isLoaded: true,
                    })
                } 
            })
    }


    public render() {
        return (
            <div className="home">
                <div className="first-container">
                    <div className="inventory-title">
                        <p>Inventory</p>
                    </div>
                    <div className="inventory">
                        <div className="inventory-content">
                        {
                            this.state.isLoaded ?
                            <ScrollableMenu plantList={this.state.plantList} hasPrice={false} />
                            :
                            <Loading />
                        }
                            
                        </div>
                    </div>
                </div>


            </div>
        )
    }
}



