import * as React from 'react';
import CloseIcon from '-!svg-react-loader!../../../media/img/error.svg';

import "./LiveViewPopUp.css";
import { ButtonPressed } from './LiveView';

interface ILiveViewPopUpProp {
    title: string;
    value: string;
    show: boolean;
    setButtonPressed(buttonPressed: ButtonPressed): void;
}

export class LiveViewPopUp extends React.Component<ILiveViewPopUpProp> {
    private onClick  = () => {
        return () => this.props.setButtonPressed(null);
    }

    public render() {
        return (
            <div className={`live-pop-up ${this.props.show && 'live-pop-up-show'}`}>
                <CloseIcon className='live-pop-close' onClick={this.onClick()}/>
                <h3>{this.props.title}</h3>
                <h1>{this.props.value}</h1>   
            </div>
        )
    }
}


