import * as React from 'react';
import './PlantItem.css';

import Cactus_plant  from '../../../media/img/cactuspg.svg';
import Flower_plant from '../../../media/img/flower.svg';
import Tomato_plant from '../../../media/img/tomato.svg';
import Corn_plant from '../../../media/img/corn.svg';
import Palm_plant from '../../../media/img/palm.svg';

interface IPlantItemProps{
    hasPrice: boolean;
    price?: number;
    name: string,
    id: number,
    balance?: number,
    onClick(id: number): void,
}

interface IPlantItemStates{
    price?: number;
    plantImage: string;
}

export class PlantItem extends React.Component<IPlantItemProps, IPlantItemStates> {

    public state ={
        price: 500, 
        plantImage: null,
    }

    public componentWillMount = () =>{
        if(this.props.hasPrice){
            this.setState({price: this.props.price});
        }
        
        switch(this.props.name) {
            case 'Palm': 
                this.setState({plantImage: Palm_plant});
                break;
            case 'Flower':
                this.setState({plantImage: Flower_plant});
                break;
            case 'Tomato':
                this.setState({plantImage: Tomato_plant});
                break;
            case 'Corn':
                this.setState({plantImage: Corn_plant});
                break;
            default:
                this.setState({plantImage: Cactus_plant});
                break;
        }

    }

    public onClick = () =>{
        this.props.onClick(this.props.id);
    }

    public render() {
        return (
            <div className={"plant-item " + ((this.props.balance >= this.props.price || !this.props.hasPrice) ? 'plant-item-show' : 'plant-item-hidden')} onClick={this.onClick}>
                <div className="image-container">
                    <img src={this.state.plantImage}/>
                </div>
                <div className="text-container">
                    <p>{this.props.name}</p>
                    {this.props.hasPrice ? this.state.price + " CB" : null}
                </div>
            </div>
        )
    }
}


