import * as React from "react";

interface IIndicatorProps {
    show: boolean;
}

import "./Indicator.css";

export const Indicator: React.StatelessComponent<IIndicatorProps> = (props: IIndicatorProps) => (
    <div className={`indicator-container ${props.show && 'indicator-show'}`}/>
)


