import * as React from "react";
import * as PropTypes from 'prop-types';
import "./Navigation.css";
import { Link } from "react-router-dom";
import LiveIcon from "-!svg-react-loader!../../../media/img/navigation/holly.svg";
import StoreIcon from "-!svg-react-loader!../../../media/img/navigation/real-estate.svg";
import InvetoryIcon from "-!svg-react-loader!../../../media/img/navigation/greenhouse.svg";
import ProfileIcon from "-!svg-react-loader!../../../media/img/navigation/woman.svg";
import { Indicator } from "./Indicator";

export default class Navigation extends React.Component {

    public static contextTypes = { router: PropTypes.object };

    public render() {
        return (
            <div className="navigation-container">
                <Link 
                    to='/mainpage/live'
                    className='navigation-buttons'
                >
                    <Indicator show={this.shouldShowIndicator('live')}/>
                    <LiveIcon />
                </Link>
                <Link 
                    to='/mainpage/inventory'
                    className='navigation-buttons'
                >
                    <Indicator show={this.shouldShowIndicator('inventory')}/>
                    <InvetoryIcon />
                </Link>
                <Link 
                    to='/mainpage/store'
                    className='navigation-buttons'
                >
                    <Indicator show={this.shouldShowIndicator('store')}/>
                    <StoreIcon />
                </Link>
                <Link 
                    to='/mainpage/profile'
                    className='navigation-buttons'
                >
                    <Indicator show={this.shouldShowIndicator('profile')}/>
                    <ProfileIcon />
                </Link>
                
            </div>
        );
    }

    private shouldShowIndicator(linkName: string): boolean {
        if (
            this.context.router &&
            this.context.router.route && 
            this.context.router.route.location && 
            this.context.router.route.location.pathname
        ) {
            const regex = new RegExp(linkName);
            return regex.test(this.context.router.route.location.pathname);
        }

        return false;
    }
}


