import * as React from 'react';
import * as PropTypes  from 'prop-types';
import { Home } from '../pages/Home/Home';
import { Store } from '../pages/Store/Store';
import { LiveView } from '../pages/LiveView/LiveView';



import './MainPage.css';
// import { Menu } from '../shared/Menu/Menu';

import {
    BrowserRouter as Router,
    Switch,
    // Link,
    Route
} from 'react-router-dom';
import Navigation from '../shared/navigation/Navigation';
import { User } from '../pages/User/User';

interface IMainPageProps {
    history: any;
}

interface IMainPageStates {
    currentPage: string;
    loggedIn: boolean;
}

export class MainPage extends React.Component<IMainPageProps, IMainPageStates> {
    public static contextTypes = { router: PropTypes.object };

    public state = {
        currentPage: "Home",
        loggedIn: true,
    };

    public handleLogout = () => {
        localStorage.clear();
        this.props.history.push("/login");
    }

    public render() {
        return (
            <div className="mainPage">
                <Router>
                    <div className="mainPage-container">
                        <div className="actionArea">
                            <Switch>
                                <Route path="/mainpage/live" component={LiveView} />
                                <Route exact={true} path='/mainpage' component={LiveView} />
                                <Route path="/mainpage/inventory" component={Home} />
                                <Route path="/mainpage/store" component={Store} />
                                <Route path="/mainpage/profile" component={User} />
                            </Switch>
                        </div>
                        <Navigation />
                    </div>
                </Router>
            </div>
        )
    }
}


