import * as React from 'react';
import Humidity from "-!svg-react-loader!../../../media/img/livebuttons/drops.svg";
import ThermometerIcon from "-!svg-react-loader!../../../media/img/livebuttons/thermometer.svg";
import Indicator from "-!svg-react-loader!../../../media/img/livebuttons/triangle.svg";
import WaterPlant from "-!svg-react-loader!../../../media/img/livebuttons/shower.svg";

import "./LiveViewButtons.css";
import { ButtonPressed } from './LiveView';

interface ILiveViewButtonsProps {
    waterPlant(): void;
    setButtonPressed(buttonPressed: ButtonPressed): void;
    currentButtonPressed: ButtonPressed;
}

export class LiveViewButtons extends React.Component<ILiveViewButtonsProps> {
    private isButtonPressed(button: ButtonPressed): boolean {
        if (this.props.currentButtonPressed === button) {
            return true
        };
        return false;
    }

    private onClick = (buttonPressed: ButtonPressed) => {
        return () => this.props.setButtonPressed(buttonPressed);
    }

    public render() {
        return (
            <div className='livew-view-buttons-container'>
                <div className='livew-view-buttons'>
                    <div className='livew-view-button water-plant-button' onClick={this.props.waterPlant}>
                        <WaterPlant />
                    </div>
                    <div className='livew-view-button humidity-button' onClick={this.onClick(ButtonPressed.HUMIDITY)}>
                        <Indicator className={`live-butons-indicator ${this.isButtonPressed(ButtonPressed.HUMIDITY) && 'live-butons-indicator-show'}`} />
                        <Humidity />
                    </div>
                    <div className='livew-view-button thermometer-button' onClick={this.onClick(ButtonPressed.THERMOMETER)}>
                        <Indicator className={`live-butons-indicator ${this.isButtonPressed(ButtonPressed.THERMOMETER) && 'live-butons-indicator-show'}`} />
                        <ThermometerIcon />
                    </div>
                </div>
            </div>
        );
    }
}


