import * as React from 'react';
import axios from 'axios';
import './Popup.css';
import exit_icon from '../../../media/img/error.svg';
import { SimpleButton } from '../../shared/buttons/SimpleButton/SimpleButton';

interface IPopupProps {
    closePopup: any,
}

export class Popup extends React.Component<IPopupProps, {}>{
    public handleFundsAdd = (value: any) =>{
        const payload = {
            "username": localStorage.getItem('username'),
            "package": value
        }

        axios.post('https://greencube-api.herokuapp.com/user-ms/add-credits', JSON.stringify(payload),
            {
                headers: {
                    'Content-Type': 'application/json'
                }
            })
            .then(res => {
                console.log(res);
                if (res.statusText === 'OK') {
                    window.location.href = '/mainpage/store';
                }
            })
    }

    public render() {
        return (
            <div className="popup">
                <div className="popupBox">
                    <div className="header">
                        <div className="popupTitle">
                            <p>Add funds</p>
                        </div>
                        <div className="closeButton">
                            <img src={exit_icon} onClick={this.props.closePopup} />
                        </div>
                    </div>
                    <div className="listBox">
                        <SimpleButton title="Tin Box - 100 Cubbux" onClick={this.handleFundsAdd.bind(this,100)}/>
                        <SimpleButton title="Bronze Box - 250 Cubbux" onClick={this.handleFundsAdd.bind(this,250)}/>
                        <SimpleButton title="Gold Box - 1600 Cubbux" onClick={this.handleFundsAdd.bind(this,1600)}/>
                    </div>
                </div>
            </div >
        )
    }
}


