import * as React from 'react';


interface IRootProps {
    history: any;
}

interface IRootState {
    loggedIn: boolean;
  }

export class RootComponent extends React.Component <IRootProps, IRootState>{
    public state = {
      loggedIn: false,
    };

    public componentDidMount(): void {
      if (localStorage.getItem('userToken')) {
        this.props.history.push("/mainpage/live");
      }
    }
    
    public render() {
      return null;
    }
}


