import * as React from "react";
import './Live.css';
import { LiveImage } from "./LiveImage";
// import { AddPlants } from "./AddPlants";
import { LiveViewButtons } from "./LiveViewButtons";
import axios from 'axios';
import { LiveViewPopUp } from "./LiveViewPopUp";
import { Loading } from "../../shared/Loading/Loading";

interface ILiveProps {
    history: any;
}

export interface IPlant {
    age?: number;
    effects?: number;
    inventory_id?: number;
    livestream_domain?: string;
    livestream_port?: number;
    plant_id?: number;
    price?: number;
    type?: string;
}

export enum ButtonPressed {
    HUMIDITY = 'humiditiy',
    THERMOMETER = 'thermometer'
}

interface ILiveState {
    plant: IPlant;
    buttonPressed: ButtonPressed;
    humidityValue: string;
    temperatureValue: string;
    plantId: string;
}



export class LiveView extends React.Component<ILiveProps, ILiveState>{
    public getStats: any;

    public state: ILiveState  = {
        plant: null,
        buttonPressed: null,
        humidityValue: '0%',
        temperatureValue: '0 C',
        plantId: '0',
    };

    public componentWillMount = () =>{
        this.setState({plantId: localStorage.getItem('plant_id')});
    }

    public componentDidMount(): void {
       this.getPlant(this.state.plantId);
       this.getStats = this.getPlantStats(this.state.plantId);
    }

    public componentWillUnmount() {
        clearInterval(this.getStats);
    }

    private async getPlant(plantId: string) {
        const response = await axios.post(
            'https://greencube-api.herokuapp.com/plant-ms/plant-data', 
            { plant_id : plantId }
        );
        this.setState({plant : response.data});
    }

    private waterPlant = (): void => {
        if (this.state.plant) {
            axios.post(
                'https://greencube-api.herokuapp.com/hardware/watering-start', 
                { plant_id : this.state.plant.plant_id }
            );
        }
    }

    private getPlantStats = (plantId: string) => {
        if (plantId) {
            return setInterval(async () => {
                const response = await axios.post(
                    'https://greencube-api.herokuapp.com/hardware/get-stats', 
                    { plant_id: plantId }
                );
    
                if (response.data) {
                    this.setState({
                        humidityValue: `${response.data.humidity}%`,
                        temperatureValue: `${response.data.temperature} C`
                    })
                }
            }            
            , 1000);
        }
    }

    private getImgUrl(): string {
        return this.state.plant && `${this.state.plant.livestream_domain}:${this.state.plant.livestream_port}/`;
    }

    private getPopUpTitle(): string {
        switch(this.state.buttonPressed) {
            case ButtonPressed.HUMIDITY: return 'Current humidity:';
            case ButtonPressed.THERMOMETER: return 'Current temperature:';
            default: return null;
        }
    }

    private getPopUpValue(): string {
        switch(this.state.buttonPressed) {
            case ButtonPressed.HUMIDITY: return this.state.humidityValue;
            case ButtonPressed.THERMOMETER: return this.state.temperatureValue;
            default: return null;
        }
    }

    private setButtonPressed = (buttonPressed: ButtonPressed) => {
        this.setState({buttonPressed});
    }

    private showPopUp(): boolean {
        return !!this.state.buttonPressed;
    }

    public render() {
        return (
            <div className='live-page'>
                {
                    this.state.plant ?
                    <React.Fragment>
                        <LiveImage url={this.getImgUrl()}/>
                        <h3 className='live-view-title'>{this.state.plant && this.state.plant.type}<span>Age: {this.state.plant && this.state.plant.age}</span></h3>
                        <LiveViewButtons 
                            waterPlant={this.waterPlant}
                            setButtonPressed={this.setButtonPressed}
                            currentButtonPressed={this.state.buttonPressed}
                        />
                        <LiveViewPopUp  
                            title={this.getPopUpTitle()}
                            value={this.getPopUpValue()}
                            show={this.showPopUp()}
                            setButtonPressed={this.setButtonPressed}
                        />
                    </React.Fragment>
                    :
                    <Loading />
                    // <AddPlants />
                }
            </div>
        );
    }
}


